import React, { useEffect, useRef, useState } from 'react';
import { useParams } from 'react-router-dom';
import styled from 'styled-components';
import { useAppSelector, useAppDispatch } from '../redux/store';
import { fetchProjectById } from '../redux/slices/projectSlice';
import { loadTimeline } from '../redux/slices/timelineSlice';
import TimelineEditor from '../components/TimelineEditor';
import TimelineToolbar from '../components/TimelineToolbar';
import VideoPreview from '../components/VideoPreview';

const TimelineContainer = styled.div`
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: ${({ theme }) => theme.colors.background};
`;

const Header = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: ${({ theme }) => theme.spacing.lg};
  background: ${({ theme }) => theme.colors.surface};
  border-bottom: 1px solid ${({ theme }) => theme.colors.border};
  height: 60px;
`;

const ProjectInfo = styled.div``;

const ProjectTitle = styled.h1`
  color: ${({ theme }) => theme.colors.text};
  font-size: 1.25rem;
  margin: 0;
`;

const ProjectDuration = styled.div`
  color: ${({ theme }) => theme.colors.textSecondary};
  font-size: 0.875rem;
`;

const MainContent = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
`;

const VideoPreviewContainer = styled.div`
  height: 300px;
  background: ${({ theme }) => theme.colors.surface};
  border-bottom: 1px solid ${({ theme }) => theme.colors.border};
`;

const TimelineSection = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
`;

const LoadingContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  color: ${({ theme }) => theme.colors.textSecondary};
`;

const LoadingSpinner = styled.div`
  width: 32px;
  height: 32px;
  border: 3px solid transparent;
  border-top: 3px solid ${({ theme }) => theme.colors.primary};
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-right: ${({ theme }) => theme.spacing.md};
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
`;

const TimelinePage: React.FC = () => {
  const { projectId } = useParams<{ projectId: string }>();
  const dispatch = useAppDispatch();
  
  const { currentProject } = useAppSelector(state => state.projects);
  const { layers, playheadTime, zoom, duration, isPlaying } = useAppSelector(state => state.timeline);
  
  const [isLoading, setIsLoading] = useState(true);
  const [videoUrl, setVideoUrl] = useState<string>('');

  useEffect(() => {
    const initializeTimeline = async () => {
      if (!projectId) return;
      
      try {
        setIsLoading(true);
        
        // Load project data
        await dispatch(fetchProjectById(projectId)).unwrap();
        
        // Load timeline data
        await dispatch(loadTimeline(projectId)).unwrap();
        
        // Set video URL
        setVideoUrl(`/api/media/${projectId}/video`);
        
      } catch (error) {
        console.error('Failed to initialize timeline:', error);
      } finally {
        setIsLoading(false);
      }
    };

    initializeTimeline();
  }, [projectId, dispatch]);

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = Math.floor(seconds % 60);
    const ms = Math.floor((seconds % 1) * 100);
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}.${ms.toString().padStart(2, '0')}`;
  };

  if (isLoading) {
    return (
      <LoadingContainer>
        <LoadingSpinner />
        Loading timeline...
      </LoadingContainer>
    );
  }

  if (!currentProject) {
    return (
      <LoadingContainer>
        Project not found
      </LoadingContainer>
    );
  }

  return (
    <TimelineContainer>
      <Header>
        <ProjectInfo>
          <ProjectTitle>{currentProject.name}</ProjectTitle>
          <ProjectDuration>
            Duration: {formatTime(duration)} | Zoom: {zoom.toFixed(1)}x
          </ProjectDuration>
        </ProjectInfo>
        
        <TimelineToolbar />
      </Header>

      <MainContent>
        <VideoPreviewContainer>
          <VideoPreview 
            videoUrl={videoUrl}
            currentTime={playheadTime}
            isPlaying={isPlaying}
          />
        </VideoPreviewContainer>

        <TimelineSection>
          <TimelineEditor 
            projectId={projectId!}
            layers={layers}
            playheadTime={playheadTime}
            zoom={zoom}
            duration={duration}
          />
        </TimelineSection>
      </MainContent>
    </TimelineContainer>
  );
};

export default TimelinePage;
